body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.header {
  font-size: 1.25rem; }

.header-title {
  display: flex;
  justify-content: center;
  background-color: #FF7F62; }

.navbar {
  display: flex;
  justify-content: space-around; }

.selected {
  color: #E1DFCE;
  text-decoration: underline; }

.tileImage {
  width: 100vw; }

.text {
  width: 50vw;
  font-size: 1.5rem;
  color: white;
  background-image: linear-gradient(#ff7f62, rgba(255, 127, 98, 0.75), rgba(255, 127, 98, 0));
  padding: 15px;
  padding-bottom: 100px; }

.right {
  position: relative;
  top: -25vh;
  left: 40%; }

.center {
  position: relative;
  top: -25vh;
  left: 25%; }

.left {
  position: relative;
  top: -25vh;
  left: 10%; }

.divider {
  width: 100vw;
  margin-bottom: 100px; }

.dividerText {
  display: flex;
  justify-content: center;
  margin: 0 20px; }

.dividerText p {
  color: #FF7F62;
  font-size: 2rem; }

.cornerBorder {
  margin: 0;
  border: #FF7F62 1px solid;
  width: 20vw;
  position: relative; }

.botRight {
  right: -80vw; }

.footer {
  background-color: #FF7F62;
  padding: 20px; }

.footer p, .footer li {
  margin-bottom: 0;
  color: white; }

